import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AdminAction from '../../../redux/actions/adminAction';

import {Col, Container, Row} from 'reactstrap'
import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";
import api from '../../../services/ApiService.js'
import {BASE_URL} from '../../../services/ApiService.js'
const roleString=[
    'User',
    'Admin'
]
class AdminUserManage extends Component{

    state = {
        users:[],
        pages       :0,

    }
    constructor(props)
    {
        super(props)
        this.table = React.createRef();
    }
    componentDidMount(){
        // this.props.getAskInvoiceCar()
        this.props.setTitle("UserManage")
                
    }
    fetchdata = (state, instance) => {
       console.log(state)
        api.get('/admin/getAllUser',{
            params:{
                pgsize  :state.pageSize,
                pg      :state.page
            }
        }).then(res=>{
            this.setState({
                users        : res.data.users,
                pages       : res.data.pgcnt,
                loading: false
             })
        })
        
    
    }
    render(){
        return(
            <div>
                <ReactTable
                                ref={this.table}
                                className="InvoiceTable"
                                getTrProps={(state, rowInfo, column) => {
                                    return {
                                        className: "cursor-pointer",
                                        onClick  : (e, handleOriginal) => {
                                            if ( rowInfo )
                                            {
                                                console.log(rowInfo.original)
                                              //  this.onShowDetail(true,rowInfo.original)
                                                //this.openEditDialog(rowInfo.original)
                                            }
                                        }
                                    }
                                }}
                                columns={[
                                    {
                                        Header:()=>{
                                            return(
                                                 <div></div>
                                             )
                                        },
                                        accessor:"avatar",
                                        
                                        Cell:t=>{
                                            return (
                                                // <img className="InvoiceTableImage" src={t.value}></img>
                                                // <div className="Text2 Inquiry-Table-Body-Item">{t.value[0]}</div>
                                                <img className="InvoiceTableImage" src={t.value ? `${BASE_URL}/upload/avatar/${t.value}` : `${BASE_URL}/upload/avatar/default.png`}></img>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div>Name</div>
                                             )
                                        },
                                        accessor:"name",
                                        
                                        Cell:t=>{
                                            return (
                                                // <img className="InvoiceTableImage" src={t.value}></img>
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>

                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Email</div>
                                             )
                                        },
                                        accessor:"email",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Address</div>
                                             )
                                        },
                                        accessor:"address",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">SeaPort</div>
                                             )
                                        },
                                        accessor:"seaport",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Role</div>
                                             )
                                        },
                                        accessor:"role",
                                        
                                        Cell:t=>{
                                            
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{roleString[t.value]}</div>
                                            )
                                        }
                                    },
                                    
                                   
                                ]}
                                defaultPageSize={5}
                                manual
                                noDataText="No contents found"
                                pages={this.state.pages}

                                data={this.state.users}
                                onFetchData={this.fetchdata}
                            />
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    auth  :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
        // getAllCars  :AdminAction.getAllCars
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminUserManage)