import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import SocketContext from '../../../socket-context'

import * as AdminAction from '../../../redux/actions/adminAction';

import {Col, Container, Row} from 'reactstrap'
import { Button , TextField } from '@material-ui/core'
import ReactTable from 'react-table-6';

import AdminInquiryDetail from './AdminInquiryDetail'
import "react-table-6/react-table.css";
import '../Admin.css'
class AdminInquiry extends Component{
    state={
        showDetail:false,
        currentInquiry:-1,
        pages       :0,
        askInquires      :[]
    }
    constructor(props){
        super(props)
        this.table = React.createRef();
    }
    componentDidMount(){
       // this.props.getAskInvoiceCar()
       this.props.setTitle("Requests")

        /** */
        /*
        this.props.socket.emit("pushuser",{
            user:this.props.auth.user
        })
        */
        /** */
       
    }
    componentWillReceiveProps(nextProps) {
        if (!nextProps.auth.isAuthenticated) {
            this.props.history.push("/");
        }
    }
    onShowDetail = (isShow,inquiry) =>{
        this.setState({
            showDetail:isShow,
            currentInquiry:inquiry ? inquiry.inquiry._id : -1
        })
        
    }
    onAcceptInvoice = (inquiry) => {

    }
    onSendMsg = (msg)=>{
   
    }
    fetchdata = (state, instance) => {
        const { totalInquiry } = this.props.admin
        let askInquires = []

        if(Array.isArray(totalInquiry)){
            askInquires = totalInquiry.filter((item)=>{
                console.log(item)
                if(item.inquiry.state == 2){
                    console.log("ok")
                    return true
                }
                return false
            })
        }
        this.setState({
            askInquires   : askInquires.slice(state.pageSize * (state.page),state.pageSize * (state.page + 1)),
            pages    : Math.ceil(askInquires.length / state.pageSize),
            loading: false
         })
     }
    render(){
        const {showDetail , currentDetailIndex} = this.state
        const { totalInquiry } = this.props.admin
        let askInquires = []
        if(Array.isArray(totalInquiry)){
            askInquires = totalInquiry.filter((item)=>{
                console.log(item)
                if(item.inquiry.state == 2){
                    console.log("ok")
                    return true
                }
                return false
            })
        }
        console.log(askInquires)
        return(
            <Row>
                {
                    !showDetail && (
                    <>
                    {
                        Array.isArray(askInquires) && (
                      
                            
                            <ReactTable
                                ref={this.table}
                                className="InvoiceTable"
                                getTrProps={(state, rowInfo, column) => {
                                    return {
                                        className: "cursor-pointer",
                                        onClick  : (e, handleOriginal) => {
                                            if ( rowInfo )
                                            {
                                                console.log(rowInfo.original)
                                                this.onShowDetail(true,rowInfo.original)
                                                //this.openEditDialog(rowInfo.original)
                                            }
                                        }
                                    }
                                }}
                                columns={[
                                    {
                                        Header:()=>{
                                            return(
                                                 <div></div>
                                             )
                                        },
                                        accessor:"inquiry.car.images[0]",
                                        
                                        Cell:t=>{
                                            return (
                                                <img className="InvoiceTableImage" src={t.value}></img>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">CarName</div>
                                             )
                                        },
                                        accessor:"inquiry.car.name",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Brand</div>
                                             )
                                        },
                                        accessor:"inquiry.car.make",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">BodyType</div>
                                             )
                                        },
                                        accessor:"inquiry.car.body_type",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Price</div>
                                             )
                                        },
                                        accessor:"inquiry.car.price",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">US$ {t.value[0].USD}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Customer</div>
                                             )
                                        },
                                        accessor:"inquiry.user.name",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Mail</div>
                                             )
                                        },
                                        accessor:"inquiry.user.email",
                                    
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">Request</div>
                                             )
                                        },
                                        accessor:"inquiry.inquiry.requestDate",
                                    
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">NewMsg</div>
                                             )
                                        },
                                        accessor:"chatlogs",
                                        Cell:t=>{
                                            //console.log(t)
                                            let hasMsg = false
                                            let cnt = 0
                                            if(Array.isArray(t.value))
                                            {
                                                t.value.map((item,index)=>{
                                                    // console.log(item.state)
                                                    // console.log(item.receiver)
                                                    // console.log(this.props.auth.user.id)
                                                    if(item.receiver._id == this.props.auth.user.id && item.state == 1)
                                                    {
                                                        
                                                        hasMsg = true
                                                        cnt++
                                                    }
                                                })
                                            }
                                            return (

                                                <div className="Text2 Inquiry-Table-Body-Item">
                                                    <div className={hasMsg?`NEWMSG-ALARM`:`NONEWMSG-ALARM`}>{hasMsg?cnt:'No msg'}</div>
                                                </div>
                                            )
                                        }
                                    },
                                ]}
                                defaultPageSize={5}
                                manual
                                
                                data={this.state.askInquires}
                                pages={this.state.pages}
                                onFetchData={this.fetchdata}
                            />
                        )
                    }
                    </>
                    )
                }
                {
                    showDetail && (
                        <>
                            <AdminInquiryDetail currentinquiry = {this.state.currentInquiry} onBack={()=>{this.onShowDetail(false,null)}} onAcceptInvoice = {this.onAcceptInvoice} sendmsg={this.onSendMsg}>
                            </AdminInquiryDetail>
                        </>
                    )
                }
            </Row>
        )
    }
}
const AdminInquiryWithSocket = (props) => (
    <SocketContext.Consumer>
        {socket => <AdminInquiry {...props} socket={socket}/>}
    </SocketContext.Consumer>
)
const mapStateToProps = (state) =>({
    auth        :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminInquiryWithSocket)