import React, {Component} from 'react';

import {TextField,MenuItem,Tab, Tabs,Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';
import {Col, Container, Row} from 'reactstrap'
//import AdminNewHowToGroupItem from './AdminNewHowToGroupItem'
import AdminNewHowToSmallItem from './AdminNewHowToSmallItem'
import '../Admin.css'
const typeString=['Normal','Specific']
class AdminNewHowToItem extends Component{
    state={
        _id:-1,
        type:1,
        title:'Step Title',
        sort:0,
        smallItems:[],
        hasCurrentEditItem:false,

        //
        isEditing:false,
        showDetail:true

    }
    componentDidMount(){
        this.setStatefromProps(this.props)
        
        
        this.setState({
            isEditing:this.props.isEditing
        })
        
        
    }
    componentWillReceiveProps(nextProps){
        console.log('change')
        console.log(nextProps)
        this.setStatefromProps(nextProps)
    }
    setStatefromProps = (props)=>{
        if(props.item){
            console.log(props.item.title)
            const { _id, sort, type, title, smallItem } = props.item
            let smallItems = []
            if(Array.isArray(smallItem)){
                smallItem.map((item,index)=>{
                    let textArray = []
                    let imageArray = []
                    if(Array.isArray(item.txtItem)){
                        item.txtItem.map((txt,index)=>{
                            textArray.push(txt.content)
                        })
                    }
                    if(Array.isArray(item.imageItem)){
                        item.imageItem.map((img,index)=>{
                            imageArray.push({
                                filename:img.url
                            })
                        })
                    }
                    
                    smallItems.push({
                        _id:item._id,
                        title:item.title,
                        textArray:textArray,
                        imageArray:imageArray
                    })
                })
            }
            this.setState({
                _id:_id,
                sort:sort,
                title:title,
                type:type,
                smallItems:smallItems
            },()=>console.log(this.state))
        }
    }
    onTilteChange = (e)=>{
        this.setState({
            title:e.target.value
        })
    }
    onSortChange = (e) =>{
        this.setState({
            sort:e.target.value
        })
    }
    onTypeChange = (e)=>{
        this.setState({
            type:e.target.value
        })
    }
    onSave = ()=>{

        if(!this.state.hasCurrentEditItem)
        {
            this.setState({
                isEditing:false
            })
            this.props.onSave({
                ...this.state
            })
        }
    }
    onEdit = ()=>{
        this.setState({
            isEditing:true
        })
    }
    onDelete = ()=>{
        this.props.onDelete(this.state._id)
    }
    addOneSmallItem = () =>{
        if(!this.state.hasCurrentEditItem)
        {
            this.setState({
                hasCurrentEditItem:true
            })
        }
    }
    onSaveSmallItem = (item) =>{
        let { smallItems } = this.state

        let existIndex = -1
        if(item._id != -1){
            smallItems.find((itemp,index)=>{
                if(itemp._id == item._id)
                {
                    existIndex = index
                    return true
                }
            })
            if(existIndex != -1)
                smallItems.splice(existIndex,1)    
        }
            
        smallItems.push(item)

        this.setState({
            smallItems:smallItems,
            hasCurrentEditItem:false
        })
    }
    onDeleteSmallItem = (index)=>{
    //    console.log(index)
        
        let temp = [...this.state.smallItems]
    //    console.log(this.state.smallItems)
        temp.splice(index,1)
    //    console.log(temp)
        
        this.setState({
            smallItems:temp
        },()=>console.log(this.state.smallItems))
        
    }
    render(){
        //const { onSave }  = this.props
        const { hasCurrentEditItem ,smallItems ,showDetail,isEditing } = this.state
        return(
            <div className="HowTo-Item">
                <Row>
                    {isEditing && (
                        <div  className="HowTo-Item-Title" >
                            <TextField
                                onChange={this.onTilteChange}
                                variant="outlined"
                                label="Title" 
                                fullWidth
                                value={this.state.title}
                                className="NormalInput"
                            />
                            <TextField
                                onChange={this.onSortChange}
                                variant="outlined"
                                label="Step" 
                                type="Number"
                                style={{width:'10%',marginLeft:'10px'}}
                                value={this.state.sort}
                                className="NormalInput"
                            />
                            <TextField
                                onChange={this.onTypeChange}
                                variant="outlined"
                                // label="Step" 
                                // type="Number"
                                style={{width:'10%',marginLeft:'10px'}}
                                value={this.state.type}
                                className="NormalInput"
                                select
                            >
                                <MenuItem value={1}>Normal</MenuItem>
                                <MenuItem value={2}>Specific</MenuItem>
                            </TextField>
                            
                        </div>
                    )}
                    {!isEditing && (
                        <div  className="HowTo-Item-Title" onClick={()=>{this.setState({showDetail:!this.state.showDetail})}}>
                            <h4 style={{fontWeight:'400'}}>
                                {this.state.title}({this.state.sort})({typeString[this.state.type - 1]})
                            </h4>
                            
                        </div>
                    )}
                        
                </Row>
                {this.state.showDetail && (
                    <>
                        <Row>
                            {smallItems.map((smallItem,index)=>{
                          //      console.log(index)
                          //      console.log(smallItem)
                                return(
                                    <>
                                        {/* {smallItem.title} */}

                                    <AdminNewHowToSmallItem key={index} index={index} showDelete={isEditing} onDelete={this.onDeleteSmallItem} isEditing={false} item={smallItem}>
                                    </AdminNewHowToSmallItem>
                                    </>
                                )
                            })}
                        </Row>
                        <Row>
                        {hasCurrentEditItem && (
                            <AdminNewHowToSmallItem isEditing={true} onSave={this.onSaveSmallItem}/>
                        )}
                        </Row>
                        <Row>
                            <div className="Howto-ToolArea">
                                {isEditing && (
                                    <>
                                        <Button className="NormalBtn" onClick={this.addOneSmallItem}>+</Button>
                                        <Button className="NormalBtn" onClick={this.onSave}>save</Button>
                                    </>
                                )}
                                {!isEditing && (
                                    <>
                                        <Button className="NormalBtn" onClick={this.onEdit}>edit</Button>
                                        <Button className="NormalBtn" onClick={this.onDelete}>delete</Button>
                                    </>
                                )}
                                
                            </div>
                        </Row>
                    </>
                )}
                
            </div>
        )
    }
}
export default AdminNewHowToItem