import React, {Component} from 'react';
import axios from 'axios'; 

import {TextField,MenuItem,Tab, Tabs,Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';
import {Col, Container, Row} from 'reactstrap'
import {DropzoneDialog} from 'material-ui-dropzone'

import {BASE_URL} from '../../../services/ApiService'
import '../Admin.css'

class AdminNewHowToSmallItem extends Component{
    state={
        _id:-1,
        title:'',
        textArray:[],
        currenttext:'',
        imageArray:[],

        //
        showUploadDlg:false,
        showDetail:true
    }
    componentDidMount(){
        this.setState({
            ...this.props.item
        })
    }
    componentWillReceiveProps(nextProps){
        this.setState({
            ...nextProps.item
        })
    }
    onSave = ()=>{
        this.props.onSave({
            ...this.state
        })
    }
    onDelete=()=>{
        this.props.onDelete(this.props.index)
    }
    onTextChange = (e)=>{
        this.setState({
            currenttext:e.target.value
        })
    }
    onTilteChange = (e)=>{
        this.setState({
            title:e.target.value
        })
    }
    addString = ()=>{
        console.log('asdf')
        let {textArray,currenttext} = this.state
        
        textArray.push(currenttext)
        this.setState({
            currenttext:'',
            textArray:textArray
        },()=>{console.log(this.state)})
    }
    uploadMultiFiles = (files) =>{
        // alert(this.state.currentState)
        // return
        //let currentInquiry = this.getCurrentInquiry()
       
        const formData = new FormData()

        for (const file of files) {
            formData.append('files[]', file, file.name);
        }
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            },
            params:{
                mode:'howto',
                //inquiryId:currentInquiry.inquiry._id

            }
        }
        axios.post(`${BASE_URL}/api/uploadfile`, formData,config)
        .then(res=>{
            let { files } = res.data
            /*
            this.props.uploadDoc({
                inquiryId:currentInquiry.inquiry._id,
                state:this.state.currentState,
                files:files
            })
            */
           console.log(files)
            this.setState({
                imageArray:files,
                showUploadDlg:false
            })
        })
        
    }
    render(){
        const { textArray,imageArray } = this.state 
        const { isEditing } = this.props
        return(
            <div className="HowTo-smallItem">

                <Row>
                    {isEditing && (
                        <TextField
                            onChange={this.onTilteChange}
                            variant="outlined"
                            label="Title" 
                            fullWidth
                            value={this.state.title}
                            className="NormalInput"
                        />
                    )}
                    {!isEditing && (
                        <div className="HowTo-Item-Title" onClick={()=>{this.setState({showDetail:!this.state.showDetail})}}>
                            <h5>
                                {this.state.title}
                            </h5>
                        </div>
                    )}
                    
                </Row>
                {this.state.showDetail && (
                    <>
                    <Row>
                    <Col md={6}>
                        <div className="Howto-smallItem-Textarea">
                        {textArray.map((text,index)=>{
                            return(
                                <div className="Howto-smallItem-TextItem">
                                    {text}
                                </div>
                            )
                            
                        })}
                        </div>
                    </Col>
                    <Col md={6}>
                        <div className="Howto-smallItem-Imagearea">
                        {
                            imageArray.map((image,index)=>{
                                return(
                                        <div className="Howto-smallItem-ImageItem">
                                        <img  src={`${BASE_URL}/upload/howto/${image.filename}`}/>
                                        </div>
                                    
                                )
                            })
                        }
                        </div>
                    </Col>
                </Row>
                {isEditing && (
                    <>
                        <Row>
                            <Col md={6} style={{alignSelf:'center',display:'flex',justifyContent:'space-between'}}>
                                <TextField
                                    onChange={this.onTextChange}
                                    variant="outlined"
                                    label="text" 
                                    fullWidth
                                    value={this.state.currenttext}
                                    style={{marginRight:'10px'}}
                                    className="NormalInput"
                                />
                                <Button className="NormalBtn" onClick={this.addString}>AddString</Button>
                            </Col>
                            <Col md={6} style={{textAlignLast: 'right'}}>
                                <DropzoneDialog
                                    open={this.state.showUploadDlg}
                                    onSave={(files)=>this.uploadMultiFiles(files)}
                                    //acceptedFiles={['image/jpeg', 'image/png', 'image/bmp', 'image/json' ]}
                                    acceptedFiles={['image/png', 'image/jpg']}
                                    
                                    showPreviews={true}
                                    maxFileSize={5000000}
                                    onClose={()=>{this.setState({showUploadDlg:false})}}
                                />
                                <Button className="NormalBtn" onClick={()=>this.setState({showUploadDlg:true})}>AddImage</Button>
                            </Col>
                        </Row>
                        
                        
                        <Row>
                            <div className="Howto-ToolArea">
                                <Button className="NormalBtn" onClick={this.onSave}>save</Button>
                            </div>
                        </Row>
                    </>
                )}
                {!isEditing && (
                     <Row>
                     <div className="Howto-ToolArea">
                        {this.props.showDelete && (
                            <Button className="NormalBtn" color="secondary" onClick={this.onDelete}>delete</Button>
                        )}
                     </div>
                 </Row>
                )}
                    </>
                )}
                
                
            </div>
        )
    }
}

export default AdminNewHowToSmallItem