import React, {Component} from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import SocketContext from '../../../socket-context'
import * as AdminAction from '../../../redux/actions/adminAction';
import ReactTable from 'react-table-6';
import {Col, Container, Row} from 'reactstrap'
import AdminOrderDetail from './AdminOrderDetail'
import "react-table-6/react-table.css";
import '../Admin.css'

const stateString = [
    'PaymentProof',
    'ShipmentSchedule',
    'Done',
    '',
    '',
    '',
    'Expired'
]
  /**
     * 0:going
     * 1:done
     * 2:expired
     * 3:cancel
     */
const invoiceState = [
    'going',
    'done',
    'expired',
    'cancel'
]
class AdminOrders extends Component{
    state={
        showDetail:false,
        currentInquiry:-1,
        pages       :0,
        orders      :[]

    }
    componentDidMount(){
        this.props.setTitle("Orders")
    }
  
    componentWillReceiveProps(nextProps) {
        if (!nextProps.auth.isAuthenticated) {
            this.props.history.push("/");
        }
    }
    onShowDetail = (isShow,inquriy) =>{
        console.log(inquriy)
        this.setState({
            showDetail:isShow,
            currentInquiry:inquriy ? inquriy.inquiry._id : -1

        })
    }
    fetchdata = (state, instance) => {
        const { totalInquiry } = this.props.admin
        let orders = []

        if(Array.isArray(totalInquiry)){
            orders = totalInquiry.filter((item)=>{
                if(item.inquiry.state > 2){
                    return true
                }
                return false
            })
        }
        this.setState({
            orders   : orders.slice(state.pageSize * (state.page),state.pageSize * (state.page + 1)),
            pages    : Math.ceil(orders.length / state.pageSize),
            loading: false
         })
     }
    render(){
    
        const {showDetail} = this.state

        return(
            <Row>
                {
                    !showDetail && (
                    <>
            {
                Array.isArray(this.state.orders) && (
                    <ReactTable
                    ref={this.table}
                    className="InvoiceTable"
                    getTrProps={(state, rowInfo, column) => {
                        return {
                            className: "cursor-pointer",
                            onClick  : (e, handleOriginal) => {
                                if ( rowInfo )
                                {
                                    console.log(rowInfo.original)
                                    this.onShowDetail(true,rowInfo.original)
                                    //this.openEditDialog(rowInfo.original)
                                }
                            }
                        }
                    }}
                    columns={[
                        {
                            Header:()=>{
                                return(
                                     <div></div>
                                 )
                            },
                            accessor:"inquiry.car.images[0]",
                            
                            Cell:t=>{
                                return (
                                    <img className="InvoiceTableImage" src={t.value}></img>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">CarName</div>
                                 )
                            },
                            accessor:"inquiry.car.name",
                            
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">Brand</div>
                                 )
                            },
                            accessor:"inquiry.car.make",
                            
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">BodyType</div>
                                 )
                            },
                            accessor:"inquiry.car.body_type",
                            
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">Price</div>
                                 )
                            },
                            accessor:"inquiry.car.price",
                            
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">US$ {t.value[0].USD}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">Customer</div>
                                 )
                            },
                            accessor:"inquiry.user.name",
                            
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">Mail</div>
                                 )
                            },
                            accessor:"inquiry.user.email",
                        
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">OrderStart</div>
                                 )
                            },
                            accessor:"inquiry.acceptDate",
                        
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">Step</div>
                                 )
                            },
                            accessor:"inquiry.state",
                        
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">
                                        <div>
                                        {stateString[t.value - 4]}
                                        </div>
                                    </div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">State</div>
                                 )
                            },
                            accessor:"inquiry.inquiryState",
                        
                            Cell:t=>{
                                return (
                                    <div className="Text2 Inquiry-Table-Body-Item">
                                        <div>
                                        {invoiceState[t.value]}
                                        </div>
                                    </div>
                                )
                            }
                        },
                        {
                            Header:()=>{
                                return(
                                     <div className="Text1 Inquiry-Table-Title-Item">NewMsg</div>
                                 )
                            },
                            accessor:"chatlogs",
                            Cell:t=>{
                             //   console.log(t)
                                let hasMsg = false
                                let cnt = 0
                                if(Array.isArray(t.value))
                                {
                                    t.value.map((item,index)=>{
                                        // console.log(item.state)
                                        // console.log(item.receiver)
                                        // console.log(this.props.auth.user.id)
                                        if(item.receiver._id == this.props.auth.user.id && item.state == 1)
                                        {
                                            
                                            hasMsg = true
                                            cnt++
                                        }
                                    })
                                }
                                return (

                                    <div className="Text2 Inquiry-Table-Body-Item">
                                        <div className={hasMsg?`NEWMSG-ALARM`:`NONEWMSG-ALARM`}>{hasMsg?cnt:'No msg'}</div>
                                    </div>
                                )
                            }
                        },
                    ]}
                    defaultPageSize={5}
                    manual
                    data={this.state.orders}
                    pages={this.state.pages}
                    onFetchData={this.fetchdata}

                />
                )
                    }
                    </>
                    )
                }
                {
                    showDetail && (
                        <>
                            <AdminOrderDetail currentinquiry = {this.state.currentInquiry} onBack={()=>{this.onShowDetail(false,null)}} onAcceptInvoice = {this.onAcceptInvoice} sendmsg={this.onSendMsg}>
                            </AdminOrderDetail>
                        </>
                    )
                }
            </Row>
        )
    }
}
const mapStateToProps = (state) =>({
    auth        :state.auth,
    admin  :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
        getOrderInvoice:AdminAction.getOrderInvoice
        // getAskInvoiceCar:AdminAction.getAskInvoiceCar ,
        // addInvoiceMsg:AdminAction.addInvoiceMsg
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminOrders)