import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as DetailProductAction from '../../redux/actions/detailProductAction';
import * as GlobalAction from '../../redux/actions/globalAction';
import {Link} from 'react-router-dom';

import {Col, Container, Row} from 'reactstrap'

import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { TextField, Button } from '@material-ui/core';
// import ImageList from '@material-ui/core/ImageList';
// import ImageListItem from '@material-ui/core/ImageListItem';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import "./CarInfoDetail.css";
import DialogComponent from '../../components/DialogComponent';
import api from '../../services/ApiService.js'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
  });
class CarInfoDetail extends Component{

    state={
        item:0,
        mail:'',
        disableButton: false
    }
    componentDidMount(){
        //console.log(this.props.match.params.id)
        let {id} = this.props.match.params
        //alert("asdf")
        api.get('/cars/getCarInfo',{params:{
            carId:id
        }}).then(res=>{
            this.setState({
                item:res.data.car
            })
        })
    }
    onSendMail=()=>{
        if(this.state.mail == ''){
            this.props.showDlg({
                type:'error',
                title:'Input error',
                msg:'Please input your mail address'
            })
        }else
        {
            if(this.props.auth.isAuthenticated){
                this.setState({
                    disableButton:true
                })
                this.props.sendMail({
                    mail:this.state.mail,
                    cid:this.state.item._id,
                    uid:this.props.auth.user.id
                },()=>{
                    this.setState({
                        disableButton:false
                    })
                })
            }else
            {
                this.props.showDlg({
                    type:'fail',
                    title:'Log in to continue',
                    msg:'Please enter or create new account to continue'
                })
            }
        }
        
        
    }
    onChangeMail = (event) => {
        this.setState({
            mail:event.target.value
        })
    }
    render(){
        const {item} = this.state
        console.log(item)

        let gallery = []
        if(Array.isArray(item.images))
        {
            item.images.map((image,index)=>{
                gallery.push({
                    original:image,
                    thumbnail:image
                })
            })
        }
        if(item){
        return(
            <ThemeProvider theme={myTheme}>

            <div className="global">
         <Row>
                  <Col md={2} xs={12}>
                     
                {/* <a href="/">Homepage</a> > productDetail */}
                </Col>
            </Row> 
            <Row>
                <Col md="7" sm="12">
                    <div className="colContent">

                        <Card style={{ boxShadow:'none'
                        }}>
                            <ImageGallery style={{maxHeight:'200px'}} items={gallery} />                       
                        </Card>
                    </div>
                </Col>
                <Col md="5" sm="12">
                    <div className="colContent" style={{height:'100%'}}>
                        <Card style={{height:'100%',boxShadow:'none'}}>
                            <div style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        justifyContent: 'space-between',
                                        height:'100%',
                                        fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
                                         opacity:'.9'
                                    }}>
                                <div style={{
                                    display: 'flex',
                                    justifyContent:"space-between",
                                    margin: '10px',
                                    padding: '5%'
                                }}>
                                    <div >
                                        <Typography gutterBottom variant="h5" component="h2" style={{fontWeight:'bold'}}>
                                            {item.make} {item.model}
                                        </Typography>
                                        <Typography gutterBottom variant="h6" style={{fontSize:'18px'}} component="h2">
                                            {item.build_year}
                                        </Typography>
                                    </div>
                                    <div>
                                        <Typography gutterBottom variant="h5" style={{fontWeight:'bold',color:'green'}} component="h2">
                                            ${item.price[0].USD}
                                        </Typography>
                                    </div>
                                </div>
                                <div className="vechileInfoCategory">
                                    <div>
                                        <p className="vechileInfoTitle">
                                            About the Vechile
                                        </p>
                                    </div>
                                    <div style={{
                                           display: 'flex',
                                           flexDirection: 'column',
                                           justifyContent: 'space-between',
                                           width: '100%',
                                           height: '100%',
                                    }}>
                                        <Row>
                                            <Col xs="4" className="vechileDetail">
                                                <img className="vechileDetailImage" src="/static/assets/img/detail/odometer.png"></img>
                                                Odometer
                                            </Col>
                                            <Col xs="8">
                                                {item.odometer}
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col xs="4" className="vechileDetail">
                                                <img className="vechileDetailImage" src="/static/assets/img/detail/engine.png"></img>
                                                Engine
                                            </Col>
                                            <Col xs="8">
                                                {item.engine_size}
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col xs="4" className="vechileDetail">
                                                <img className="vechileDetailImage" src="/static/assets/img/detail/vin.png"></img>
                                                VIN
                                            </Col>
                                            <Col xs="8">
                                                {item.vin}
                                            </Col>
                                        </Row>

                                    </div>
                                </div>
                                <div className="vechileInfoCategory" >
                                    <div>
                                        <p className="vechileInfoTitle">
                                            Send me detailed car report
                                        </p>
                                    </div>
                                    <div >
                                        <TextField
                                            id="outlined-password-input"
                                            name="email"

                                            label="Email"
                                            type="email"
                                            variant="outlined"
                                            validators={['required']}
                                            errorMessages={['this field is required']}
                                            value={this.state.mail}
                                            onChange={this.onChangeMail}
                                            fullWidth
                                            
                                        />
                                    </div>
                                    <Row>
                                        <Col md="4" sm="12">
                                            <Button 
                                                fullWidth 
                                                color="primary" 
                                                variant="contained" 
                                                style={{textTransform:'none',color:'white', height:'50px',marginTop:'10%'}} 
                                                onClick={this.onSendMail}
                                                disabled={this.state.disableButton}
                                            >
                                                Send
                                            </Button>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </Card>
                    </div>
                            
                </Col>
              
                
            </Row>
            <DialogComponent/>

            </div>
            </ThemeProvider>
        )
        }else
        {
            return(
                <></>
            )
        }
    }
}
const mapStateToProps = (state) =>({
    auth:state.auth,
    //item:state.detailProduct.item
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
      sendMail:DetailProductAction.sendMail,
      showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(CarInfoDetail)