import {
    HOME_GET_GLOBAL_DATA,
     GET_CAR,
     SET_ADVANCED_FILTER,
     CHANGE_SORTBY,
     SET_FAVORITE,
     SET_SHOWFILTER,
     SET_COUNTRY,
     HOME_SET_ISFAVORITE
} from './types'

import api from '../../services/ApiService.js'
export const setCountry = (index) => dispatch=>{
    dispatch({
        type:SET_COUNTRY,
        payload:index,
    })
}
export const getGlobalData = (param) =>dispatch =>{
    api.get('/users/getglobaldata',{
        params:param
    })
    .then(res=>{
        /*
        dispatch({
            type:HOME_GET_GLOBAL_DATA,
            payload:res.data.favorites
        })
        */
    })
}
export const setSearchString = (searchString,filter) => dispatch=>{
    

    getCarWithFilter({
        'advanced_filter':filter,
        'searchString':searchString
    },dispatch)
    
    dispatch({
        type:SET_ADVANCED_FILTER,
        filter:filter,
        searchString:searchString
    })
}
export const setCarFilter = (searchString,filter) => dispatch=>{
    

    getCarWithFilter({
        'advanced_filter':filter,
        'searchString':searchString
    },dispatch)
    
    dispatch({
        type:SET_ADVANCED_FILTER,
        filter:filter,
        searchString:searchString
    })
    
}
export const getCarWithFilter = (filter,dispatch) =>{
    

    console.log(filter)
    api.get('/cars/filter',{
        params:filter,
        
    })
    .then(res=>{
        dispatch({
            type:GET_CAR,
            payload:{
                data:res.data,
                isfavorite:false
            }
        })
    })
}
export const showFavorites = (param) => dispatch =>{
    dispatch({
        type:HOME_SET_ISFAVORITE,
        payload:{
            isfavorite:true
        }
    })
    

    api.get('/cars/favorite',{
        params:param
    })
    .then(res=>{
        dispatch({
            type:GET_CAR,
            payload:{
                data:res.data,
                isfavorite:true
            }
        })

    })
}
export const getCar = (param) => dispatch =>{
    
    api.get('/cars',{param})
    .then(res=>{
        console.log('car')
        console.log(res.data)
        dispatch({
            type:GET_CAR,
            payload:{
                data:res.data,
                isfavorite:false
            }
        })
    })
}
export const setSortBy = (sortIndex) => dispatch=>{
    //console.log('sortybyaction')
    dispatch({
        type:CHANGE_SORTBY,
        payload:sortIndex
    })
}
export const setFavorite = (param,isAuthenticated) => dispatch=>{
    
    if(isAuthenticated){
        api.post('/users/setfavorite',{
            param
        })
        .then(res=>{
            dispatch({
                type:SET_FAVORITE,
                payload:res.data
            })        
        })
    }else
    {
        dispatch({
            type:SET_FAVORITE,
            payload:param
        })        
    }
}
export const getFavorite = (param) => dispatch =>{
    api.post('/users/getfavorite',{
        param
    })
    .then(res=>{
        dispatch({
            type:SET_FAVORITE,
            payload:res.data
        })        
    })
}
export const clearFavorite = (param) => dispatch =>{
        console.log("clear")
        dispatch({
            type:SET_FAVORITE,
            payload:{
                favorite:[]
            }
        })  
}
export const setShowFilter = () =>dispatch=>{
    dispatch({
        type:SET_SHOWFILTER
    })
}