import React, {Component} from 'react';
import {TextField,MenuItem, Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';
import {Col, Container, Row} from 'reactstrap'
import {DropzoneDialog} from 'material-ui-dropzone'
import {BASE_URL} from '../../../services/ApiService'
import axios from 'axios'; 

import '../Admin.css'

class AdminCountryDialog extends Component{
    state={
        name:"",
        flag :"default.png",
        txtValueGroup:[]
    }
    componentDidMount(){
        this.setState({
            txtKeygroup:this.props.txtKeygroup
        })
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if ( !prevProps.info.props.open && this.props.info.props.open )
        {
           
            // if(this.props.info.data &&
            // !_.isEqual(this.props.info.data,prevState))
            console.log(this.props.info)
            {
                this.setState({...this.props.info.data},()=>console.log(this.state))
            }

        }
        //console.log(this.state)
    }
    submitData = () =>{
        this.props.submit(this.state)
    }
    handleChange = (e) =>{
        if(e.target.name=='name')
        {
            this.setState({
                name:e.target.value
            })
        }
    }  
    keygroupchange = (e)=>{
        let txval = [...this.state.txtValueGroup]
        let index = txval.findIndex((item)=>item.name==e.target.name)
        if(index == -1){
            txval.push({name:e.target.name,value:e.target.value})
        }else
        {
            txval[index].value = e.target.value
        }
        this.setState({
            txtValueGroup:txval
        })
    }
    handleSelectFile = e =>{
        let file = e.target.files[0]
        const formData = new FormData()
        formData.append('file',file)
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            },
            params:{
                mode:'flag',
            }
        }
        return axios.post(`${BASE_URL}/api/uploadsinglefile`, formData, config)
            .then(res =>{
                if(res.data.file){
                    this.setState({
                        flag:res.data.file.filename
                    })
                }
                
            })
    }
    render(){
        const { info,txtKeygroup } = this.props;

        return(
            <Dialog
                classes={{
                    paper: "m-24"
                }}
                {...info.props}
                onClose={this.props.close}
                fullWidth
                maxWidth="lg"
                >
                    <DialogContent classes={{root: "p-24"}} style={{textAlign: 'center'}}>
                        <div className="CountryNamePart">
                            <div className="CountryNamePart-Name">
                                <TextField
                                    className="mb-24"
                                    label="CountryName"
                                    autoFocus
                                    id="balance"
                                    name="name"
                                    type="string"
                                    variant="outlined"
                                    value={this.state.name}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>
                            <div className="CountryNamePart-flag">
                                <label htmlFor="flag" style={{width:'100%',height:'100px'}}>
                                <img  src={`${BASE_URL}/upload/flag/${this.state.flag != 0?this.state.flag:'default.png'}`} style={{width:'100px',borderRadius:'50%',border:'1px solid black'}}/>
                                <input style={{display:'none'}} id="flag" type='file' name='flag' onChange={this.handleSelectFile} />
                                </label>
                            </div>
                        </div>
                        <div className="CountryKeyPart">
                            <div className="CountryKeyItemContent">
                                <div className="CountryKeyItemContent-Key">
                                    <p>Key</p>
                                </div>
                               <div className="CountryKeyItemContent-Value">
                                   content
                               </div>
                            </div>
                        {Array.isArray(txtKeygroup) && (
                            <>
                            {txtKeygroup.map((txt)=>{
                                let val = this.state.txtValueGroup.find((item)=>item.name==txt)
                                return(
                                            
                                        <div className="CountryKeyItemContent">
                                            <div className="CountryKeyItemContent-Key">
                                                <p>{txt}</p>
                                            </div>
                                            <div  className="CountryKeyItemContent-Value">
                                            <TextField
                                                className="mt-8 mb-16"
                                                id="amount"
                                                name={txt}
                                                label="content"
                                                type="string"
                                                value={val?val.value:""}
                                                onChange={this.keygroupchange}
                                                required
                                                fullWidth
                                                variant="outlined"
                                            />
                                            </div>
                                        </div>
                                )
                            })}
                            </>
                        )}
                         
                        </div>
               </DialogContent>
               <DialogActions className="justify-between pl-16">
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={this.submitData}
                            //disabled={!this.canBeSubmitted()}
                        >
                            Save
                        </Button>
                </DialogActions>
            </Dialog>
        )
    }
}
export default AdminCountryDialog