import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AdminAction from '../../../redux/actions/adminAction';
import {Col, Container, Row} from 'reactstrap'
import {TextField,MenuItem,Tab, Tabs,Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';

import AdminNewHowToItem from './AdminNewHowToItem'
class AdminHowToNormal extends Component{
    state={
        hasCurrentEditItem:false,
        howToItems:[]
    }
    componentDidMount(){
        this.props.setTitle("How-to-buy")
        this.props.getHowToBuy()
    }
    
    onAddHowToItem = () =>{
        if(!this.state.hasCurrentEditItem)
        {
            this.setState({
                hasCurrentEditItem:true
            })

        }
    }
    onSaveHowToItem = (item)=>{
       // alert("sdf")
        /*
        let { howToItems } = this.state
        
        let existIndex = -1
        if(item._id != -1){
            howToItems.find((itemp,index)=>{
                if(itemp._id == item._id)
                {
                    existIndex = index
                    return true
                }
            })
            if(existIndex != -1)
                howToItems.splice(existIndex,1)    
        }
        */  

        //howToItems.push(item)
        console.log(item)
        
        this.props.saveHowToItem({
            item:item
        })
        
        this.setState({
           // howToItems:howToItems,
            hasCurrentEditItem:false
        },()=>{console.log(this.state)})

    }
    onDeleteHowto = (id) =>{
        console.log(id)
        console.log("DELETE")
        this.props.deleteHowtoItem({
            id:id
        })
    }
    render(){
        const { normalHowto } = this.props.admin
        const { hasCurrentEditItem } = this.state
        
        return(
            <div>
                <Row>
                {normalHowto.map((howto,index)=>{
                    return(
                        <>
                        
                        <AdminNewHowToItem key={index} isEditing={false} onDelete = {this.onDeleteHowto}  onSave={this.onSaveHowToItem} item={howto}>
                        </AdminNewHowToItem>
                        </>
                    )
                })}
                </Row>
                <Row>
                {hasCurrentEditItem && (
                    <AdminNewHowToItem isEditing={true} onSave={this.onSaveHowToItem}/>
                )}
                </Row>
                <Row>
                    <Button onClick={this.onAddHowToItem} > + </Button>
                </Row>
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    auth  :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
        getHowToBuy:AdminAction.getHowToBuy,
        saveHowToItem:AdminAction.saveHowToItem,
        deleteHowtoItem:AdminAction.deleteHowtoItem,
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminHowToNormal)