import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import {Link} from 'react-router-dom';

import {Col, Container, Row} from 'reactstrap'
import { Button , MenuItem , Select } from '@material-ui/core'
import MyDropDown from '../Utility/MyDropDown'
import {BASE_URL} from '../../services/ApiService'

class Foot extends Component
{
    state={
        currencyDropDownList:[
            {
                firstimage:'./static/assets/img/flag/us.png',
                title:'USD'
            },
            {
                firstimage:'./static/assets/img/flag/eu.png',
                title:'EUR'
            },
            {
                firstimage:'./static/assets/img/flag/aud.png',
                title:'AUD'
            }
        ],
        countryDropDownList:[],
        countryDropDownHeader:[],
    }
    componentDidMount(){
     //   alert("asdf")
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.global.countries.length != 0){
            let countries =[]
            nextProps.global.countries.map((country,index)=>{
                countries.push({
                    firstimage:`${BASE_URL}/upload/flag/${country.flag}`,
                    title:country.name,
                    value:index
                })
            })
      
            this.setState({
                countryDropDownList:countries,
                countryDropDownHeader:{
                    firstimage:`${BASE_URL}/upload/flag/${nextProps.global.countries[nextProps.global.country].flag}`,
                    title:nextProps.global.countries[nextProps.global.country].name
                }
            })
            
           console.log(countries)
        }
    }
    
    setCurrencyChange = (event) =>{
        this.props.setCurrentCurrency(event.target.value)
    }
    setCurrencyDropdownChange = (index,element)=>{
        this.props.setCurrentCurrency(element.title)
    }
    setCountryDropdownChange = (country,element) =>{
        //alert(country)
        this.props.selectCountry(country)
       
    }
    render(){
        const { currencyDropDownList,countryDropDownHeader,countryDropDownList } = this.state;

        let currencyHeader 
        currencyDropDownList.map((item,index)=>{
            if(item.title == this.props.global.currentCurrency)
            {
                currencyHeader = {
                    firstimage:item.firstimage,
                    title:item.title
                }
            }
        })
        let date = new Date()
        
        let currentYear = date.getFullYear()
        return(
            <div className='foot' style={{display:'flex',justifyContent:'center',margin:'30px 30px 30px 30px'}}>
                <div style={{width:'80%'}}>
                <Row>
                    <Col md='4' xs='12' style={{textAlign:'left'}}>
                        <p style={{color:'var(--main-color)',margin:'20px 0px 20px 0px'}}><h1>Logo</h1></p>
                        <p>1772 Nevskaya Street NW, Suite 21389, Atlanta, GA 902344</p>
                        <p style={{color:'var(--main-color)'}}>hello@carsfromaustralia.com</p>
                        <p>Copyright  {currentYear}. All rights reserved.</p>

                    </Col>
                     <Col md='4' xs='12'  style={{textAlign:'left'}}>
                        {/* <div>
                            <Button>Search</Button>
                        </div>
                        <div>
                            <Button>How TO BUY</Button>
                        </div>
                        
                        {
                            this.props.auth.isAuthenticated == false && (
                                <>
                                <div>
                                    <Link to = "/signin" className="nav-link"><Button fullWidth  color="primary" style={{minWidth:'150px'}}>Sign In</Button></Link>
                                </div>
                                <div>
                                    <Link to = "/signup" className="nav-link"><Button fullWidth variant='contained' style={{backgroundColor:'rgba(73, 154, 236, 1)',minWidth:'150px'}} color="primary" block>Sign Up</Button></Link>
                                </div>
                                </>
                            )
                        }
                        {
                            this.props.auth.isAuthenticated == true && (
                                <>
                                    isAuthenticated
                                </>
                            )
                        } */}
                    </Col> 
                    <Col md='4' xs='12'>
                        <Row>
                        <Col md='6' xs='12'>
                        <MyDropDown header={currencyHeader} dropdownlist={currencyDropDownList} top={true} hasoutline={false.toString()} color='primary' isCheck={true.toString()} onChange={this.setCurrencyDropdownChange}></MyDropDown>
                        </Col>
                        <Col md='6' xs='12'>
                        <MyDropDown header={countryDropDownHeader} dropdownlist={countryDropDownList} top={true} hasoutline={false.toString()} color='primary' isCheck={true.toString()} onChange={this.setCountryDropdownChange}></MyDropDown>                            
                        </Col>
                        </Row>
                        {/* <Select
                                labelId="demo-simple-select-outlined-label"
                                id="demo-simple-select-outlined"
                                //value={this.state.currency}
                                value={this.props.global.currentCurrency}
                                variant="outlined"
                                onChange={this.setCurrencyChange}
                                fullWidth
                                style={{
                                    maxHeight:'40px'
                                }}
                                >
                                <MenuItem value="USD">
                                    USD
                                </MenuItem>
                                <MenuItem value={'EUR'}>EUR</MenuItem>
                                <MenuItem value={'AUD'}>AUD</MenuItem>
                            </Select> */}
                    </Col>
                </Row>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state) =>({
    auth:state.auth,
    global:state.global,
    favorite:state.home.favorite
})
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        logoutUser:AuthAction.logoutUser,
        selectCountry           :GlobalAction.setCountry,

        setCurrentCurrency:GlobalAction.setCurrentCurrency
    },dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(Foot)