import {
    SET_HOWTO_BUY
 } from './types'
import api from '../../services/ApiService.js'
export const getHowtobuy = (param) => dispatch=>{
    
    api.get('/howtobuy/',{
        params:param
    })
    .then(res=>{
        dispatch({
            type:SET_HOWTO_BUY,
            payload:res.data
        })
    })
    
}
