import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as GlobalAction from '../redux/actions/globalAction'
import {Modal,Typography,Button} from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
  });
class DialogComponent extends Component{
    render(){
        return(
            <ThemeProvider theme={myTheme}>

            <Modal style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
          }}
              open={this.props.show}
              onClose={()=>{this.props.hide()}}
              disableBackdropClick={true}
              aria-labelledby="simple-modal-title"
              aria-describedby="simple-modal-description"
          >
          
              <div style={{
                 
                  width: '25%',
                  minWidth:'250px',
                  backgroundColor: 'white',
                //   border: '2px solid #15507B',
                  borderRadius:'10px',
                  padding:'30px',
                  margin:'10px',
                  textAlign:'left',
                  alignItems:'center',
                  textAlign:'center',
                  position:'relative'
              }}>
                    <div style={{
                        
                        width:'100px',
                        height:'100px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'-50px',
                        left:'50%',
                        transform:'translateX(-50%)'
                    }}>
                        <img src={`/static/assets/img/dlg/${this.props.dlgInfo.type}.png`} style={{width:'70%',height:'70%',borderRadius:'50%',marginTop:'15%'}}></img>
                    </div> 
                    <div style={{
                        
                        width:'40px',
                        height:'40px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'5px',
                        right:'15px'
                        
                    }}>
                         {/* <img src={`./static/assets/img/dlg/${this.props.dlgInfo.type}.jpg`} onClick={()=>{this.props.hide()}} style={{width:'100%',height:'100%',cursor:'hand'}}></img> */}
                    </div> 
                  
                  <Typography style={{width:'100%',color:'black',fontSize:'25px',padding:'40px 20px 10px 20px',fontWeight:'bold',opacity:'0.8'}}>
                      {this.props.dlgInfo.title}
                  </Typography>
                  <Typography style={{width:'100%',color:'black',padding:'10px 20px 20px 10px',opacity:'0.6'}}>
                  {this.props.dlgInfo.msg}
                  </Typography>
                 <Button onClick={()=>{
                     this.props.hide()
                     console.log(this.props.dlgInfo.closeAction)
                     if(this.props.dlgInfo.closeAction)
                     {
                        this.props.dlgInfo.closeAction()
                     }
                     
                    }} color="primary" style={{textTransform:'none', color:'white',width:'50%',height:'45px',margin:'10px 20px 20px 20px'}} variant='contained'>Continue</Button>
              </div>
              
          </Modal>
          </ThemeProvider>
                    
                
        )
    }
}
const mapStateToProps = (state) =>({
    show:state.global.showDialog,
    dlgInfo:state.global.dlgInfo,
    
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        hide :GlobalAction.hideDialog
    }, dispatch);
}


export default connect(mapStateToProps,mapDispatchToProps)(DialogComponent)