import {
    SHOW_DIALOG,
    USER_LOADING, 
    USER_LOADED, 
    AUTH_ERRORS,
    SET_CURRENT_USER, 
    LOGIN_SUCCESS, 
    LOGIN_FAIL,
    REGISTER_START, 
    REGISTER_DONE,
    
    LOUGOUT_USER, 
    LOGOUT_SUCCESS,
    AUTH_CHANGE_USER
} from './types'
import api from '../../services/ApiService.js'
import * as GlobalAction from './globalAction'
import setAuthToken from "../../utils/setAuthToken";
import jwt_decode from "jwt-decode";

export const registerUser = (param,callback) => dispatch =>{
    console.log('register')
    dispatch(loading());
    console.log(callback)
    api.post('/auth/register',{param})
    .then(res => {
        console.log(res.data)
        if(res.data.success == 3){
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'success',
                    title:'We have sent verification mail to you',
                    msg:'Please check your mail app',
                    closeAction:null
                    
                }
            })
        }else
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'fail',
                    title:'SignUp Failed!',
                    msg:'Your email  is already exist!',
                    closeAction:null
                }
            })
        }
        dispatch({
            type:REGISTER_DONE,
            payload:res.data.success
        })
        //alert('donebefore')
        callback()
    })
}
