export const logoutUser = () => dispatch =>{
    localStorage.removeItem("CFAToken");
    setAuthToken(false);
    dispatch(setCurrentUser(null));
}
export const setCurrentUser = decoded => {
    return {
      type: SET_CURRENT_USER,
      payload: decoded
    };
  };
  
  // User loading
export const loading = () => {
    return {
        type : REGISTER_START
    }
}
export const changeAvatar = (param) => dispatch=>{
    api.post('/auth/changeAvatar',{param})
    .then(res => {
        if(res.data.result == true){
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'success',
                    title:'Update Success',
                    msg:'',
                    closeAction:null
                }
            })
            localStorage.setItem('CFAToken',res.data.token)
            const { token } = res.data;
            localStorage.setItem("CFAToken", token);
           const decoded = jwt_decode(token);
           dispatch(setCurrentUser(decoded))
        }else{
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'error',
                    title:'Invalid Input',
                    msg:'Your input maybe wrong',
                    closeAction:null
                }
            })
        }
    })
}
