import {SET_DETAIL_PRODUCT,SHOW_DIALOG,SEND_EMAIL} from './types'
import api from '../../services/ApiService.js'

export const setDetailProduct = (item) => dispatch=>{
    dispatch({
        type:SET_DETAIL_PRODUCT,
        payload:item
    })
}
export const sendMail = (param,callback) => dispatch =>{

    api.post('/detail/sendmail',{param})
    .then(res=>{
        if(res.data.result == true)
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'success',
                    title:'Successful',
                    msg:'We have sent car report to your email. Please check your mailbox'
                }
            })
        }else
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'fail',
                    title:'Sent Failed!',
                    msg:'Something wrong when processing'
                }
            })
        }
        callback()
        dispatch({
            type:SEND_EMAIL,
            payload:res.data.result
        })
    })
}