import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AdminAction from '../../../redux/actions/adminAction';

import {Col, Container, Row} from 'reactstrap'
import {Fab,TextField,MenuItem,Tab, Tabs,Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';

import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";
import AdminCountryDialog from './AdminCountryDialog'
import {BASE_URL} from '../../../services/ApiService'
import AddIcon from '@material-ui/icons/Add';
import api from '../../../services/ApiService.js'
  /**
     * 0:default
     * 1:inquiry
     * 2:ask invocie but not confirm
     * 3:confirm invoice
     * 4:waiting payment proof
     * 5:shipment schedule
     * 6:done
     * 
     */
const invoiceStateString = [
    ''
]
class AdminCountryManage extends Component{

    state = {
        countries:[],
        pages       :0,
        dialogInfo  :{
            props:{
                open:false
            },
            data:null
        },
        
        txtKeygroup:[]

    }
    constructor(props)
    {
        super(props)
        this.table = React.createRef();
    }
    componentDidMount(){
        // this.props.getAskInvoiceCar()
        this.props.setTitle("CountryManage")
        
        this.fetchHowToBuyKey()        
    }
    fetchHowToBuyKey = ()=>{
        api.get('/admin/getHowtobuyKey')
        .then((res)=>{
            if(res.data.result)
            {
                let totalSpecificHowto = res.data.totalSpecificHowto
                console.log(totalSpecificHowto)
             
                let txtKeygroup = []
                let imgKeygroup = []
                totalSpecificHowto.map((item,index)=>{
                    
                    if(Array.isArray(item.smallItem)){
                        item.smallItem.map((sitem,sindex)=>{
    
                            if(Array.isArray(sitem.txtItem)){
                                sitem.txtItem.map((txt)=>{
                                    txtKeygroup.push(txt.content)
                                })
                            }
                        })
                    }
                   
                })
                console.log(txtKeygroup)

                this.setState({
                    txtKeygroup:txtKeygroup
                })
            }
        })

    }
    openNewDialog = ()=>{
        this.setState({
            dialogInfo:{
                props:{
                    open:true
                },
                data:null
            }
        })
    }
    openEditDialog = (info) =>{
        this.setState({
            dialogInfo:{
                props:{
                    open:true
                },
                data:info
            }
        })
    }
    
    closeEditdialog = () =>{
        this.setState({
            dialogInfo:{
                props:{
                    open:false
                },
                data:null
            }
        })
    }
    submitData = (info) =>{
        console.log(info)
        //return
        api.post('/admin/newCountry',{
                params  :info,
        }).then(res=>{
            if(res.data.result)
                this.fetchdata(this.table.current.state,0)
         
        })
        
        this.closeEditdialog()
    }
    fetchdata = (state, instance) => {
       console.log(state)
        api.get('/admin/getAllCountryInfo',{
            params:{
                pgsize  :state.pageSize,
                pg      :state.page
            }
        }).then(res=>{
            console.log(res.data)
            this.setState({
                countries   : res.data.countries,
                pages       : res.data.pgcnt,
                loading: false
             })
        })
    }
    render(){
        return(
            <div>
                
                <ReactTable
                                ref={this.table}
                                className="InvoiceTable"
                                getTrProps={(state, rowInfo, column) => {
                                    return {
                                        className: "cursor-pointer",
                                        onClick  : (e, handleOriginal) => {
                                            if ( rowInfo )
                                            {
                                                this.openEditDialog(rowInfo.original)
                                            }
                                        }
                                    }
                                }}
                                columns={[
                                    {
                                        Header:()=>{
                                            return(
                                                 <div>Flag</div>
                                             )
                                        },
                                        accessor:"flag",
                                        
                                        Cell:t=>{
                                            return (
                                                <div style={{width:'100%',textAlign:'center'}}>
                                                <img className="InvoiceTableImage" src={`${BASE_URL}/upload/flag/${t.value}`}></img>
                                                </div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div>CountryName</div>
                                             )
                                        },
                                        accessor:"name",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>

                                            )
                                        }
                                    },
                                ]}
                                defaultPageSize={5}
                                manual
                                noDataText="No contents found"
                                pages={this.state.pages}

                                data={this.state.countries}
                                onFetchData={this.fetchdata}
                            />
                              <Fab
                                    color="primary"
                                    aria-label="add"
                                    onClick={this.openNewDialog}
                                >

                                    <AddIcon/>

                                </Fab>
                            <AdminCountryDialog info={this.state.dialogInfo} txtKeygroup={this.state.txtKeygroup} submit={this.submitData} close={this.closeEditdialog}/>
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    auth  :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
        // getAllCars  :AdminAction.getAllCars
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminCountryManage)