import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import axios from 'axios'; 

import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import {Col, Container, Row} from 'reactstrap'
import { Button , Paper , TextField, MenuItem, Select } from '@material-ui/core'
import MuiPhoneNumber from "material-ui-phone-number";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

import {BASE_URL} from '../../services/ApiService'
import "./MyInquiry.css";
const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
  });
class MySettingItem extends Component{
    state={
        id:-1,
        name    :'',
        email   :'',
        phone   :'',
        password:'',
        confirmpassword:'',
        coutnry :'',
        seaport :'',
        address :'',
    }
    componentDidMount(){
        // console.log(this.props.auth.user)
        console.log(this.props.auth.user)

        const {id, name , email , phone , password , country , seaport , address } = this.props.auth.user
        this.setState({
            id:id,
            name:name,
            email:email,
            phone:phone,
            country:country,
            seaport:seaport,
            address:address
        })
    }
    componentWillReceiveProps(nxtProps){
        const {id, name , email , phone , password , country , seaport , address } = nxtProps.auth.user
        console.log(nxtProps.auth.user)
        this.setState({
            id:id,
            name:name,
            email:email,
            phone:phone,
            country:country,
            seaport:seaport,
            address:address
        })
    }
    onInputValueChange = (e) =>{
        let name = e.target.name
        console.log(name)
        this.setState({
            [name] : e.target.value
        })

    }
    phonenumberchange = (e)=>{
        this.setState({
            phone:e
        })
    }
    saveChange = () =>{
        console.log(this.state)
        if(this.state.name == '' || this.state.email == '' || this.state.password == '')
        {
            this.props.showDlg({
                type:'error',
                title:'Input error',
                msg:'Name, Email, Password fields are required'
            })
        }else
        {
            if(this.state.password == this.state.confirmpassword){
                this.props.changeUserInfo({
                    ...this.state,
                    id:this.props.auth.user.id
                })
            }else
            {
                this.props.showDlg({
                    type:'error',
                    title:'Input error',
                    msg:'Password field must be match with ConfirmPassword field'
                })
            }
        }
        
    }
    handleSelectFile = e =>{
        let file = e.target.files[0]
        const formData = new FormData()
        formData.append('file',file)
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            },
            params:{
                mode:'avatar',
                userId:this.props.auth.user.id
            }
        }
        return axios.post(`${BASE_URL}/api/uploadsinglefile`, formData, config)
            .then(res =>{
                 this.props.changeAvatar({
                     file:res.data.file,
                     user:this.props.auth.user.id
                 })
            })
    }
    render(){
        const { name , email , phone ,confirmpassword, password , country , seaport , address , myInquiryToggle , myInquiryItem } = this.state
        const { countries } = this.props.global
        return(
            <ThemeProvider theme={myTheme}>

            <Row className="TabContent">
                <div className="SettingPart">
                    <Row>
                        <Col md='3'>
                            <div className="Setting-Photo">
                                <div className="Setting-Photo-Item">
                                    <img src={this.props.auth.user.avatar ? `${BASE_URL}/upload/avatar/${this.props.auth.user.avatar}` : `${BASE_URL}/upload/avatar/default.png`}></img>
                                </div>
                                <div className="Setting-Photo-Item-Detail">
                                <label htmlFor="avatar" style={{width:'75%',height:'45px'}}>
                                    <input style={{display:'none'}} id="avatar" type='file' name='avatar' onChange={this.handleSelectFile} />
                                    <Button color="primary" component="span" style={{color:'white',height:'100%'}} fullWidth variant='contained'>
                                        Upload New
                                    </Button>
                                </label>

                                </div>
                                
                                {/* <div className="Setting-Photo-Item-Detail">
                                    <Button color="secondary" fullWidth style={{width:'75%'}}>Delete</Button>
                                </div> */}

                                <div className="Setting-Photo-Item-Detail">
                                    <p className="Opacity70">
                                        You can update and delete current photo
                                    </p>
                                </div>

                            </div>
                        </Col>
                        <Col md='9'>
                            <div className="Setting-Col-Part"  style={{height:'100%'}}>
                                <Row style={{height:'100%'}}>
                                    <Col md='6'>
                                        <div className="Setting-Content-Item">
                                        <p className="Item-Title">NAME</p>
                                        <TextField
                                            id="outlined-name"
                                            type="text"
                                            label="Name"
                                            name="name"
                                            fullWidth
                                            value={name}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>
                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                        <p className="Item-Title">COUNTRY</p>

                                        <Select
                                            labelId="demo-simple-select-outlined-label"
                                            value={country}
                                            fullWidth
                                            name="country"
                                            variant="outlined"
                                            onChange={this.onInputValueChange}
                                        >
                                           { Array.isArray(countries) && (
                                                <>
                                                {countries.map((item,index)=>{
                                                    return(
                                                        <MenuItem role="option" value={item.name}>{item.name}</MenuItem>
                                                    )
                                                })}
                                                </>
                                            )}
                                          
                                        </Select>

                                    </div>
                              
                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">EMAIL</p>

                                        <TextField
                                            id="outlined-name"
                                            type="email"
                                            label="Email"
                                            name="email"
                                            fullWidth
                                            value={email}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>

                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">SEAPORT</p>

                                        <TextField
                                            id="outlined-name"
                                            type="text"
                                            label="Seaport"
                                            name="seaport"
                                            fullWidth
                                            value={seaport}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>

                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">PHONE NUMBER</p>

                                        <MuiPhoneNumber 
                                            defaultCountry={'au'} 
                                            value={phone}
                                            onChange={this.phonenumberchange}
                                            fullWidth
                                            variant="outlined"

                                        />
                                        </div>

                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">ADDRESS</p>

                                        <TextField
                                            id="outlined-name"
                                            type="text"
                                            label="Address"
                                            name="address"
                                            fullWidth
                                            value={address}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>

                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">CHANGE PASSWORD</p>

                                        <TextField
                                            id="outlined-name"
                                            type="password"
                                            label="Password"
                                            name="password"
                                            fullWidth
                                            value={password}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>

                                    </Col>
                                    <Col md='6'>
                                    <div className="Setting-Content-Item">
                                    <p className="Item-Title">CONFIRM PASSWORD</p>

                                        <TextField
                                            id="outlined-name"
                                            type="password"
                                            label="ConfirmPassword"
                                            name="confirmpassword"
                                            fullWidth
                                            value={confirmpassword}
                                            onChange={this.onInputValueChange}
                                            variant="outlined"
                                        />
                                        </div>

                                    </Col>
                                   
                                    
                                   
                                    
                                </Row>
                            </div>
                        </Col>
                    </Row>
                        
                    <Row>
                        <div style={{width:'100%',textAlign:'right',paddingTop:'20px'}}>
                            <Button onClick={this.saveChange}  color="primary" className="NormalBtn SaveBtn" style={{height:'45px'}} variant='contained'>
                                Save
                            </Button>
                        </div>
                    </Row>
                </div>
            </Row>
            </ThemeProvider>
        )
    }
}
const mapStateToProps = (state) =>({
    auth            :state.auth,
    global          :state.global
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        changeAvatar:AuthAction.changeAvatar,
        changeUserInfo:AuthAction.changeUserInfo,
        showDlg:GlobalAction.showDialog

      // showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(MySettingItem)
