import React, {Component} from 'react';
import { withRouter } from 'react-router-dom' 
import { useHistory } from "react-router-dom";
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import * as HomeAction from '../../redux/actions/homeAction';


import * as InquiryAction from '../../redux/actions/inquiryAction';
import SocketContext from '../../socket-context'
// import createHistory from 'history/createBrowserHistory';
import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import {Nav, Navbar, NavbarToggler, Collapse, NavItem, NavLink, NavbarBrand,NavbarText,Dropdown} from 'reactstrap'
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import {Link} from 'react-router-dom';
import MyDropDown from '../Utility/MyDropDown'
import './Navbar.css';
import '../../App.css';
import {BASE_URL} from '../../services/ApiService'

const styles = theme =>({

})

class NavComponent extends Component{
    //let history = useHistory();
    state={
        isOpen:false,
        country:'Arial',
        locationDropDown:false,
        adminDropDonwList:[],
        adminDropDownHeader:[],
        countryDropDownList:[],
        countryDropDownHeader:[],

    }

    toggle = () =>{ 
        //setlocationDropDownOpen(!locationDropDown)   
        this.setState({
            locationDropDown:!this.state.locationDropDown
        })
    }
    componentDidMount(){
        this.props.getCountries()
        if(this.props.auth.isAuthenticated){
            //alert("a")
            
            this.props.getFavorite({
                favorite:this.props.home.favorite,
                uid:this.props.auth.user.id
            })
            
        }
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.global.countries.length != 0){
            let countries =[]
            nextProps.global.countries.map((country,index)=>{
                countries.push({
                    firstimage:`${BASE_URL}/upload/flag/${country.flag}`,
                    title:country.name,
                    value:index
                })
            })
      
            this.setState({
                countryDropDownList:countries,
                countryDropDownHeader:{
                    firstimage:`${BASE_URL}/upload/flag/${nextProps.global.countries[nextProps.global.country].flag}`,
                    title:nextProps.global.countries[nextProps.global.country].name
                }
            })
            
           console.log(countries)
        }
        if(nextProps.auth.isAuthenticated){
            this.props.socket.emit('getNavBarAlert',nextProps.auth.user)

            let avatarUrl = `${BASE_URL}/upload/avatar/`
            avatarUrl = nextProps.auth.user.avatar?`${avatarUrl}${nextProps.auth.user.avatar}`:`${avatarUrl}default.png`
            this.setState({
                adminDropDownHeader:{
                    title:nextProps.auth.user.name,
                    firstimage:avatarUrl
                },
                adminDropDonwList:[
                    
                {
                    title:'My Inquiry',
                    firstimage:'/static/assets/img/myinquiry.png',
                },
                {
                    title:'My Orders',
                    firstimage:'/static/assets/img/myorders.png',

                },
                {
                    title:'Settings',
                    firstimage:'/static/assets/img/settings.png',

                },
                {
                    title:'Favorites',
                    firstimage:'/static/assets/img/favorites.png',

                },
                {
                    title:'Sign Out',
                    firstimage:'/static/assets/img/myinquiry.png',

                },
                ]
            })
            
        }
    }
    
    toggleNav = () => {
        // setIsOpen(!isOpen)
        this.setState({
            isOpen:!this.state.isOpen
        })
    }
    CountryChange = (e) =>{
        this.setState({
            country:e.target.value
        })
        //setCountry(e.target.value)
        // console.log(props.auth)
    }
    onDashboard = (e) =>{
        e.preventDefault()
    }
    onInquiry = (e) =>{
        e.preventDefault()
       // props.history.push('./myinquiry')
    }
    onLogout = (e) =>{
        e.preventDefault()
        console.log("logout")
        this.props.clearFavorite()

        //this.props.logoutUser()
    }
    setCountryDropdownChange = (country,element) =>{
        //alert(country)
        this.props.selectCountry(country)
       
    }
    clickAdminDropdown = (index , element)=>{
        //alert(index)
        switch(element.title){
            case 'My Inquiry':
            case 'My Orders':
            case 'Settings':
                this.props.history.push('/myinquiry')
               
                  /*
                const history = createHistory();
                history.push('/myinquiry');

                */
                this.props.setCurrentInquiryPage(index)
                break
            case 'Favorites':
                this.props.showFavorites({
                    uid:this.props.auth.user.id
                })
                this.props.history.push('/')
                
                break
            case 'Sign Out':
                this.props.clearFavorite()

                this.props.logoutUser()
                break
        }
    }
    render(){
        const { isOpen,countryDropDownHeader,countryDropDownList,adminDropDownHeader,adminDropDonwList } = this.state
        return(
            <Navbar light expand = "lg" className="MyNavBar">
      
                <NavbarBrand href="/"><p style={{color:'var(--main-color)'}}><h1>Logo</h1></p></NavbarBrand>
                <NavbarToggler onClick={this.toggleNav}></NavbarToggler>
                <Collapse isOpen = {isOpen} navbar>
                    <Nav className = "ml-auto" navbar>
                        <NavItem>
                            <Link to = "/how-to-buy" className="nav-link">
                                <Button fullWidth color="light" style={{minWidth:'150px',height:'40px',fontWeight:'500'}}>HOW TO BUY</Button>
                            </Link>
                        </NavItem>

                        <NavItem style={{minWidth:'200px'}}>
                            <MyDropDown header={countryDropDownHeader} dropdownlist={countryDropDownList} hasoutline={false.toString()} color='primary' isCheck={true.toString()} onChange={this.setCountryDropdownChange}></MyDropDown>                            
                        </NavItem> 
                    
                        {
                            this.props.auth.isAuthenticated == false && (<>
                                <NavItem>
                                    <Link to = "/signin" className="nav-link"><Button fullWidth  color="primary" style={{minWidth:'150px',fontWeight:'500'}}>Sign In</Button></Link>
                                </NavItem>

                                <NavItem>
                                    <Link to = "/signup" className="nav-link"><Button fullWidth variant='contained' style={{color:'white', minWidth:'150px',fontWeight:'500'}} color="primary" block>Sign Up</Button></Link>
                                </NavItem>
                                </>)
                        }
                        {
                            this.props.auth.isAuthenticated == true && (
                            <>
                                <NavItem style={{minWidth:'200px'}}>
                                    <MyDropDown header={adminDropDownHeader} dropdownlist={adminDropDonwList} hasoutline={false.toString()} color='secondary' isCheck={false} onChange={this.clickAdminDropdown}></MyDropDown>                            
                                </NavItem>
                                
                                {
                                    (this.props.auth.user.role == 1 && (
                                        <NavItem>
                                            <Link to = "/admin/" className="nav-link"><Button fullWidth  color="primary" style={{minWidth:'150px'}}>Admin Dashboard</Button></Link>
                                        </NavItem>
                                    ))
                                }
                            </>)
                        }
                    </Nav>
                </Collapse>
            </Navbar>
  
    )
    }
    
}
const NavComponentWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <NavComponent {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth:state.auth,
    home:state.home,
    global:state.global
})
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        getCountries            :GlobalAction.getCountry,
        selectCountry           :GlobalAction.setCountry,
        setCurrentInquiryPage   :InquiryAction.setCurrentPage,
        getFavorite             :HomeAction.getFavorite,
        clearFavorite           :HomeAction.clearFavorite,
        showFavorites           :HomeAction.showFavorites,
        //setNewChatLogs          :GlobalAction.setNewChatLogs,
        logoutUser              :AuthAction.logoutUser
    },dispatch);
}
export default (withStyles(styles, {withThemem: true})(connect(mapStateToProps,mapDispatchToProps)(withRouter(NavComponentWithSocket))))