import React, {Component} from 'react';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import "./HowToBuy.css";

class HowToBuyStepBar extends Component{
    render(){
        const {howtobuy,current} = this.props

        return(
            <div className="HowtoBuyStepbar">
                {howtobuy.map((item,index)=>{
                    let hasArrow = true
                    if(index == howtobuy.length - 1)
                        hasArrow = false
                    let linkClassName="HowToBuy_LinkItem"
                    if(current == index)
                        linkClassName = `${linkClassName} current`
                    return(
                        <div className="HowtoBuyStepbar_Item">
                            <div style={{marginRight:'10px'}}>
                                {current == index && (
                                    <>
                                    <Link style={{color:'#499AEC'}} onClick={()=>this.props.onClickItem(index)} activeClass="active" className="test1" to={`howtobuy_${item.title}`} spy={true} smooth={true} duration={500} >{`Step ${index + 1} `}</Link>
                                    {hasArrow && (
                                        <>
                                            {''}
                                        </>
                                    )}
                                    </>    
                                )}
                                {current != index && (
                                    <>
                                    <Link style={{color:'black'}} onClick={()=>this.props.onClickItem(index)} activeClass="active" className="test1" to={`howtobuy_${item.title}`} spy={true} smooth={true} duration={500} >{`Step ${index + 1} `}</Link>
                                    {hasArrow && (
                                        <>
                                            {``}
                                        </>
                                    )}
                                    </>    
                                )}
                            </div>
                            
                        </div>
                    )
                })}
            </div>
        )
    }
}
export default HowToBuyStepBar