import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as HomeAction from '../redux/actions/homeAction';
import * as GlobalAction from '../redux/actions/globalAction'
import { makeStyles } from '@material-ui/core/styles';
import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import AdvancedSearch from '../components/AdvancedSearch/AdvancedSearch';
import CarInfo from '../components/CarInfo/CarInfo';
import './Home.css';
import TextField from '@material-ui/core/TextField';
import {Col, Container, Row} from 'reactstrap'
import Button from '@material-ui/core/Button'
import {Paper,Divider,InputBase,IconButton,Avatar} from '@material-ui/core'
import DirectionsIcon from '@material-ui/icons/Directions';
import SearchIcon from '@material-ui/icons/Search';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
      secondary:{
        main: 'rgba(255, 255, 255, 1)',
      }
    },
  });
const styles = theme=> ({
    root: {
      //padding: '2px 4px',
      display: 'flex',
      alignItems: 'center',
      width: '100%',
    //   height:'50px',
      boxShadow:'none',
      boxShadow:'1px 1px 5px 5px #f3f3f3'

      //margin:'15px'
    },
    search:{
        marginLeft:'10px',
        opacity:'0.5'
    },
    input: {
      marginLeft: theme.spacing(1),
      flex: 1,
      fontColor:'grey'
    },
    iconButton: {
      padding: 10,
      width:'120px'
    },
    divider: {
      height: 55,
      margin: 4,
    },
  });
let socket = null

class  Home extends Component{
    state={
        searchString:'',
        test:true
    }
    componentDidMount(){
        if(this.props.auth.isAuthenticated){
            this.props.getGlobalData({
                uid:this.props.auth.user.id
            })
        }
        
        if(!this.props.home.isfavorite)
            this.props.setFilter(this.props.home.SearchString,this.props.filter)
        
    }
    onChangeSearchString = (event)=>{
        //alert(event.target.value)
        this.setState({
            searchString:event.target.value
        })
    }
    setSearchString = () =>{
        //alert("asdf")
        this.props.setSearchString(this.state.searchString,this.props.home.advancedfilter)
        this.setState({
         //   searchString:''
        })
    }
    onFilter = () =>{
        this.props.setShowFilter()
    }
    render(){
        const {classes} = this.props;

        return(
            <ThemeProvider theme={myTheme}>

            <React.Fragment>
                    <Row>
                        
                        <Col md="12" style={{display:'flex'}}>
                            <div className="colContent">
                                <Paper component="form" className={classes.root}>
                                    <SearchIcon className={classes.search} />
                                    <InputBase
                                        className={classes.input}
                                        placeholder="Search"
                                        onChange={this.onChangeSearchString}
                                        value={this.state.searchString}
                                        onKeyPress={(e)=>{
                                            if (e.key === 'Enter') {
                                                    e.preventDefault()
                                                    this.setSearchString()
                                                }
                                        }}
                                    />
                                    <Divider className={classes.divider} orientation="vertical" />
                                    <Button
                                        onClick={this.onFilter}
                                        style={{color:'rgba(73, 154, 236, 1)',paddingLeft:'25px',paddingRight:'25px'}}
                                        startIcon={<Avatar style={{
                                            width:'35px'
                                        }} src={'./static/assets/img/Filter.svg'} />}
                                    >
                                        Filters
                                    </Button>
                                    
                                </Paper>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        {this.props.isShowFilter && (
                            <>
                            <Col lg="3" md="4" sm="12">
                                <div className="colContent">
                                    <AdvancedSearch/>
                                </div>
                            </Col>
                            <Col lg="9" md="8" sm="12">
                            <div className="colContent">

                                <CarInfo isShowFilter={this.props.isShowFilter}/>
                                </div>
                            </Col>
                            </>
                        )}
                        {!this.props.isShowFilter && (
                            <>
                            <Col sm="12">
                                <div className="colContent">

                                    <CarInfo isShowFilter={this.props.isShowFilter}/>
                                </div>
                            </Col>
                            </>
                        )}
                   
                    </Row>
                
            </React.Fragment>
            </ThemeProvider>
        )
    }
    
}
const mapStateToProps = (state) =>({
    auth:state.auth,
    home:state.home,
    filter:state.home.advancedfilter,

    isShowFilter:state.home.isShowFilter,
    cars:state.cars,
    socket:state.global.socket
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setSearchString     :HomeAction.setSearchString,
        setFilter           :HomeAction.setCarFilter,
        getGlobalData       :HomeAction.getGlobalData,
        getCar              :HomeAction.getCar,
        setShowFilter       :HomeAction.setShowFilter,
        setSocket           :GlobalAction.setSocket

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)((withStyles(styles, {withThemem: true})(Home)));
