const {
    SET_HOWTO_BUY,

} = require('../actions/types');

const initialState = {
    howtobuy:[]
}
export default function(state = initialState,action){
    switch(action.type){
        case SET_HOWTO_BUY:
            return{
                ...state,
                howtobuy:action.payload.normalHowto
            }
        default:
            return state
    }
}
